import React, { useState, useEffect, useRef } from 'react';
import Switch from '@material-ui/core/Switch';
import { IconButton } from '@material-ui/core';
import FlipCameraIosIcon from '@material-ui/icons/FlipCameraIos';
import CameraAltIcon from '@material-ui/icons/CameraAlt';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import CircularProgress from '@material-ui/core/CircularProgress';
import Webcam from 'react-webcam';
import '@tensorflow/tfjs-backend-cpu';
import * as bodyPix from '@tensorflow-models/body-pix';
import { Template } from './templates/Template';

const UseWindowDimensions = () => {
    const getWindowDimensions = () => {
        const { innerWidth: width, innerHeight: height } = window;
        return { width, height }
    }
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());
    useEffect(() => {
        const onResize = () => {
            setWindowDimensions(getWindowDimensions());
        }
        window.addEventListener('resize', onResize);
        return () => window.removeEventListener('resize', onResize);
    }, []);
    return windowDimensions;
}

interface ButtonsProps {
    isCameraEnable: boolean
    setCameraEnable: Function
    isCaptured: boolean
    setCaptured: Function
    shutter: boolean
    setShutter: Function
    isDetected: boolean
    setDetected: Function
    facingMode: string
    setFacingMode: Function
    isBackgroundEnable: boolean
    setBackgroundEnable: Function
    processing: boolean
}
const Buttons: React.FC<ButtonsProps> = ({
    isCameraEnable,
    setCameraEnable,
    isCaptured,
    setCaptured,
    shutter,
    setShutter,
    isDetected,
    setDetected,
    facingMode,
    setFacingMode,
    isBackgroundEnable,
    setBackgroundEnable,
    processing
}) => {
    return (
        <div
            style={{
                display: 'flex',
                justifyContent: 'space-around',
                alignItems: 'center'
            }}
        >
            {/* camera start or capture button */}
            <IconButton disabled={processing}>
                {isCaptured ||
                    <CameraAltIcon color={processing ? 'disabled' : 'primary'} onClick={() => setShutter(true)}/>
                }
                {isCaptured &&
                    <CameraAltIcon 
                        color={processing ? 'disabled' : 'primary'} 
                        onClick={() => {
                            setCaptured(false)
                            setDetected(false)
                        }}
                    />
                }
            </IconButton>

            {/* select camera */}
            <IconButton disabled={isCaptured || processing}>
                {(isCaptured) && 
                    <FlipCameraIosIcon color='disabled'/>
                }
                {isCaptured ||
                    <>
                        {facingMode === 'user' &&
                            <FlipCameraIosIcon color={processing ? 'disabled' : 'primary'} onClick={() => setFacingMode('environment')}/>
                        }
                        {facingMode === 'environment' &&
                            <FlipCameraIosIcon color={processing ? 'disabled' : 'primary'} onClick={() => setFacingMode('user')}/>
                        }
                    </>
                }
            </IconButton>

            {/* background */}
            <Switch defaultChecked color='primary' disabled={!isCaptured} onChange={() => setBackgroundEnable(!isBackgroundEnable)} />

            {/* upload */}
            <IconButton disabled={true}>
                <CloudUploadIcon color='disabled'/>
            </IconButton>
        </div>
    );
}

interface CanvasProps {
    webcamRef: any
    facingMode: string
    width: number
    height: number
    isCaptured: boolean
    setCaptured: Function
    isBackgroundEnable: boolean
    shutter: boolean
    setShutter: Function
    isDetected: boolean
    setDetected: Function
    processing: boolean
    setProcessing: Function
    capturedTime: string
    setCapturedTime: Function
    peopleCount: number
    setPeopleCount: Function
    background: HTMLImageElement
    cameraSize: number[]
    setCameraSize: Function
}

const Canvas: React.FC<CanvasProps> = ({
    webcamRef,
    facingMode,
    width,
    height,
    isCaptured,
    setCaptured,
    isBackgroundEnable,
    shutter,
    setShutter,
    isDetected,
    setDetected,
    processing,
    setProcessing,
    capturedTime,
    setCapturedTime,
    peopleCount,
    setPeopleCount,
    background,
    cameraSize,
    setCameraSize
}) => {
    const screenshotRef = useRef(true);
    const drawCanvasRef = useRef(true);
    const detectRef = useRef(true);
    const [model, setModel] = useState<any>(null);
    const [predictions, setPredictions] = useState<any>(null);
    const [screenshot, setScreenshot] = useState<HTMLImageElement>(new Image);
    const canvasRef = useRef<HTMLCanvasElement>(null);

    const GetCameraSize = async () => {
        console.log('start getting camera size.');
        setTimeout(() => {
            if (webcamRef.current) {
                const webcamCurrent = webcamRef.current as any;
                console.log('webcam ready.');
                console.log(webcamCurrent.video.readyState);
                if (webcamCurrent.video.readyState === 4) {
                    const imgSrc = webcamRef.current?.getScreenshot();
                    let img = new Image;
                    img.src = imgSrc;
                    img.onload = () => {
                        console.log('natural width: '+img.naturalWidth);
                        console.log('natural height: '+img.naturalHeight);
                        setCameraSize([img.naturalWidth, img.naturalHeight]);
                        console.log('cameraSize: '+cameraSize);
                        console.log('end getting camera size.');
                    }
                }
            }
        }, 2000);
    }
    const GetScreenshot = async () => {
        console.log('start screenshot.');
        setTimeout(() => {
            if (webcamRef.current) {
                const webcamCurrent = webcamRef.current as any;
                console.log('webcam ready.')
                if (webcamCurrent.video.readyState === 4) {
                    const imgSrc = webcamRef.current?.getScreenshot();
                    let img = new Image;
                    img.src = imgSrc;
                    const date = new Date();
                    setCapturedTime(date.getFullYear()+'/'+(date.getMonth()+1)+'/'+date.getDate()+' '+date.getHours()+':'+date.getMinutes()+':'+date.getSeconds());
                    img.onload = () => {
                        console.log('end screenshot.');
                        setScreenshot(img);
                        console.log('screenshot width: '+img.naturalWidth);
                        console.log('screenshot height: '+img.naturalHeight);
                        setShutter(false);
                        setCaptured(true);
                    }
                }
            }
        }, 2000);
    }
    const Detect = async () => {
        setPredictions(null);
        if (isCaptured) {
            console.log('detect start.');
            setProcessing(true);
            /*
            const preds = await model.segmentMultiPerson(screenshot, {
                flipHorizontal: false,
                internalResolution: 'medium',
                segmentationThreshold: 0.7
            });
            */
            const internalResolution = 'full';
            //const preds = await model.segmentMultiPersonParts(screenshot, {
            const preds = await model.segmentMultiPerson(screenshot, {
                internalResolution: internalResolution
            });
            console.log('detect end.');
            if (preds.length) {
                console.log(preds);
                setPredictions(preds);
            } else {
                console.log('no detection.');
            }
            setProcessing(false);
            setDetected(true);
        }
    }
    const DrawCanvas = async () => {
        if (isCaptured) {
            if (canvasRef.current) {
                console.log('canvas ready.');
                console.log(canvasRef.current.width);
                console.log(screenshot.naturalWidth);
                canvasRef.current.width = screenshot.naturalWidth;
                canvasRef.current.height = screenshot.naturalHeight;

                const opacity = 0.7;
                const flipHorizontal = false;
                const maskBlurAmount = 0;
                const canvas = canvasRef.current;
                //const mask = bodyPix.toMask(predictions)
                if (predictions) {
                    const foregroundColor = {r: 255, g: 0, b: 0, a: 255};
                    const backgroundColor = {r: 0, g: 0, b: 0, a: 0};
                    const mask = bodyPix.toMask(
                        predictions,
                        foregroundColor,
                        backgroundColor
                    );
                    // const mask = bodyPix.toColoredPartMask(predictions);
                    if (isBackgroundEnable) {
                        bodyPix.drawMask(
                            canvas,
                            screenshot,
                            mask,
                            opacity,
                            maskBlurAmount,
                            flipHorizontal
                        );
                    } else {
                        bodyPix.drawMask(
                            canvas,
                            background,
                            mask,
                            opacity,
                            maskBlurAmount,
                            flipHorizontal
                        );
                    }
                    const context = canvas.getContext('2d');
                    if (context) {
                        context.fillStyle = 'red';
                        context.fillText('count: '+Object.keys(predictions).length, 10, 10);
                    }
                } else {
                    const context = canvasRef.current.getContext('2d');
                    if (context) {
                        if (isBackgroundEnable) {
                            context.drawImage(screenshot, 0, 0);
                        } else {
                            context.drawImage(background, 0, 0);
                        }
                    }
                }


                console.log('drawn.');
            }
        }
    }
    const LoadModel = async () => {
        console.log('start model loading.');
        setProcessing(true);
        /*
        const architecture = 'ResNet50';
        const outputStride = 16;
        const quantBytes = 4;
        */
        const architecture = 'MobileNetV1';
        const outputStride = 16;
        const quantBytes = 2;
        const bodypix = await bodyPix.load({
           architecture: architecture,
           outputStride: outputStride,
           quantBytes: quantBytes
        });
        setModel(bodypix);
        setProcessing(false);
        console.log('model loaded.')
    }

    useEffect(() => {
        if (!processing) {
            if (Object.keys(cameraSize).length === 0) {
                GetCameraSize();
            }
        }
    }, [processing]);

    useEffect(() => {
        LoadModel();
    }, []);

    useEffect(() => {
        if (screenshotRef.current) {
            screenshotRef.current = false;
            return;
        }
        GetScreenshot();
    }, [shutter]);

    useEffect(() => {
        if (drawCanvasRef.current) {
            drawCanvasRef.current = false;
            return;
        }
        Detect();
    }, [isCaptured])

    useEffect(() => {
        if (detectRef.current) {
            detectRef.current = false;
            return;
        }
        DrawCanvas();
    }, [isDetected, isBackgroundEnable])

    console.log('shutter', shutter, 'processing', processing, 'isCaptured', isCaptured, 'condition', (isCaptured && !processing));
    return (
        <div 
            style={{
                width: width, 
                height: height*0.8,
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
            }}
        >
            {(!shutter && !isCaptured && !processing && Object.keys(cameraSize).length === 0) &&
                <Webcam
                    audio={false}
                    ref={webcamRef}
                    videoConstraints={{
                        facingMode: facingMode
                    }}
                    style={{
                        margin: 'auto',
                        textAlign: 'center',
                        zIndex: 9,
                        visibility: 'hidden'
                    }}
                />
            }
            {(!shutter && !isCaptured && !processing && Object.keys(cameraSize).length === 2) &&
                <Webcam
                    audio={false}
                    ref={webcamRef}
                    videoConstraints={{
                        facingMode: facingMode
                    }}
                    style={{
                        margin: 'auto',
                        textAlign: 'center',
                        zIndex: 9,
                        width: '100%',
                        height: '100%'
                    }}
                />
            }
            {(shutter && !isCaptured && !processing && Object.keys(cameraSize).length === 2) &&
                <Webcam
                    audio={false}
                    ref={webcamRef}
                    videoConstraints={{
                        facingMode: facingMode
                    }}
                    style={{
                        margin: 'auto',
                        textAlign: 'center',
                        zIndex: 9,
                        width: cameraSize[0],
                        height: cameraSize[1],
                        visibility: 'hidden',
                    }}
                />
            }
            {processing && 
                <CircularProgress 
                />
            }
            {(isCaptured && !processing) && 
                <>
                    <canvas
                        ref={canvasRef}
                        style={{
                            margin: 'auto',
                            width: '100%',
                            height: '100%',
                            zIndex: 30
                        }}
                    />
                </>
            }
        </div>
    );
}

interface SegmentationProps {
    background: HTMLImageElement
    cameraSize: number[]
    setCameraSize: Function
}

export const Segmentation: React.FC<SegmentationProps> = ({
    background,
    cameraSize,
    setCameraSize
}) => {
    const [isCameraEnable, setCameraEnable] = useState<boolean>(true);
    const [isCaptured, setCaptured] = useState<boolean>(false);
    const [shutter, setShutter] = useState<boolean>(false);
    const [isDetected, setDetected] = useState<boolean>(false);
    const [isBackgroundEnable, setBackgroudEnable] = useState<boolean>(true);
    const [facingMode, setFacingMode] = useState<string>('user');
    const [processing, setProcessing] = useState<boolean>(false);
    const [capturedTime, setCapturedTime] = useState<string>('');
    const [peopleCount, setPeopleCount] = useState<number>(0);
    const { width, height } = UseWindowDimensions();
    const webcamRef = useRef<Webcam>(null);
    return (
        <Template title='segmentation'>
            <Buttons
                isCameraEnable={isCameraEnable}
                setCameraEnable={setCameraEnable}
                isCaptured={isCaptured}
                setCaptured={setCaptured}
                shutter={shutter}
                setShutter={setShutter}
                isDetected={isDetected}
                setDetected={setDetected}
                facingMode={facingMode}
                setFacingMode={setFacingMode}
                isBackgroundEnable={isBackgroundEnable}
                setBackgroundEnable={setBackgroudEnable}
                processing={processing}
            />
            <Canvas
                webcamRef={webcamRef}
                facingMode={facingMode}
                width={width}
                height={height}
                isCaptured={isCaptured}
                setCaptured={setCaptured}
                isBackgroundEnable={isBackgroundEnable}
                shutter={shutter}
                setShutter={setShutter}
                isDetected={isDetected}
                setDetected={setDetected}
                processing={processing}
                setProcessing={setProcessing}
                capturedTime={capturedTime}
                setCapturedTime={setCapturedTime}
                peopleCount={peopleCount}
                setPeopleCount={setPeopleCount}
                background={background}
                cameraSize={cameraSize}
                setCameraSize={setCameraSize}
            />
        </Template>
    );
}